html,
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', 'Clear Sans', 'Helvetica Neue', sans-serif;
  line-height: 1; }

body {
  margin: 2rem 1rem;
  background: #fff;
  color: #666;
  font-size: 16px;
  font-fammily: sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1, h2, h3 {
  color: #333;
  margin: 4rem 0 0.5rem 0; }

h1, h2, h3, h4, h5, h6, p, li {
  line-height: 1.5;
  transition: font-size 0.2s cubic-bezier(0.25, 0.1, 0.25, 1); }

h1 {
  font-size: 8rem;
  margin: 0.33rem 0 0 0;
  text-transform: uppercase;
  color: #214050; }

h2 {
  font-size: 2.5rem; }

h1 + h2 {
  color: #666;
  margin-top: -1rem;
  margin-bottom: 2rem;
  line-height: 1.2; }

h3 {
  font-size: 2rem; }

h4 {
  margin: 1.5rem 0 0.5rem 0;
  font-size: 1.5rem; }

h5 {
  font-size: 1.25rem;
  display: inline; }

p {
  margin: 0.5rem 0;
  padding: 0;
  text-align: justify; }

a {
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  color: inherit;
  text-decoration: none;
  transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1); }

a:hover {
  color: #000;
  border-bottom-width: 2px;
  border-bottom-color: black; }

a:focus {
  background: rgba(0, 0, 0, 0.1);
  border: 1px rgba(0, 0, 0, 0.25) dotted;
  outline: none; }

abbr,
abbr[title] {
  border-bottom: 0; }

input {
  font-size: inherit; }

button:active {
  outline: none; }

::-webkit-selection {
  color: #fff;
  background: #9e0927; }

::-moz-selection {
  color: #fff;
  background: #9e0927; }

::selection {
  color: #fff;
  background: #9e0927; }

/*
 * -----------------------------------------------------------------------------
 * General styles, i.e. classes
 * -----------------------------------------------------------------------------
 */
.blue {
  color: #214050; }

.centered {
  text-align: center; }

.abstract {
  text-align: justify; }

.note {
  color: #999;
  font-style: italic; }

.no-list-style {
  margin: 0;
  padding: 0;
  list-style: none; }

.underlined {
  position: relative; }
  .underlined:after {
    position: absolute;
    display: block;
    content: '';
    bottom: -1px;
    left: -1rem;
    right: -1rem;
    height: 2px;
    background: #eee; }

h3.iconized,
h4.iconized,
h5.iconized {
  position: relative;
  display: block; }
  h3.iconized > *,
  h4.iconized > *,
  h5.iconized > * {
    display: block;
    float: left; }
  h3.iconized .icon,
  h4.iconized .icon,
  h5.iconized .icon {
    height: 100%;
    width: 1em;
    margin-right: 0.25em; }

h3.iconized {
  height: 3rem; }

h4.iconized {
  height: 1.5rem; }

h5.iconized {
  height: 1.25rem; }

ol.iconized,
ul.iconized {
  position: relative;
  overflow: auto; }
  ol.iconized li,
  ul.iconized li {
    position: relative;
    overflow: auto; }
    ol.iconized li > *,
    ul.iconized li > * {
      display: block;
      float: left; }
    ol.iconized li > *:last-child,
    ul.iconized li > *:last-child {
      margin-right: 0.5em; }
    ol.iconized li .icon,
    ul.iconized li .icon {
      height: 1.5em;
      width: 1em;
      margin-right: 0.25em; }

.icon-inline {
  display: inline;
  width: 0.8em;
  height: 0.8em;
  line-height: 1.5em;
  vertical-align: center; }

.inline-list li {
  display: inline; }

.social-links {
  font-size: 0.85em; }

.date {
  display: block; }

.two-authors li:first-child:after {
  content: ' and '; }

.video {
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.reference .secondary-information {
  font-size: 0.85em; }

/*
 * -----------------------------------------------------------------------------
 * Specific styles, i.e. IDs
 * -----------------------------------------------------------------------------
 */
#authors h4 + p {
  margin-top: -0.25rem; }

#youtube {
  position: relative; }
  #youtube:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 55.35714%; }
  #youtube > .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }

/*
 * -----------------------------------------------------------------------------
 * Media Queries
 * -----------------------------------------------------------------------------
 */
/* 568px (Pure.css Grid: SM) */
/* 640px */
@media screen and (min-width: 40em) {
  body {
    margin-left: 8.3334%;
    /* 1/12 */
    margin-right: 8.3334%; }

  .video {
    width: 90%; } }
/* 960px */
@media screen and (min-width: 60em) {
  body {
    font-size: 18px;
    margin-left: 11.1112%;
    /* 1/9 */
    margin-right: 11.1112%; }

  .video {
    width: 80%; } }
/* 1280px */
@media screen and (min-width: 80em) {
  body {
    font-size: 20px;
    margin-left: 16.66667%;
    /* 1/6 */
    margin-right: 16.66667%; }

  .video {
    width: 70%; } }
/* 1600px */
@media screen and (min-width: 100em) {
  body {
    font-size: 22px;
    margin-left: 20%;
    /* 1/5 */
    margin-right: 20%; } }


